%fontRoboto {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
}

%defauilScrollY {
  &::-webkit-scrollbar {
    width: 8px;
  }
  
  &::-webkit-scrollbar-track,
  &::-webkit-scrollbar-thumb {
    border-radius: $smallBrdrRadius;
  }
  
  &::-webkit-scrollbar-thumb {
    background: $submitBtnColor;
  }
}
